import util,{_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

const emptyObject = {		
		is_push_wechat: 1,					//是否推送微信消息
		int_day: '',						//考勤日期
		int_start_hour: '',					//开始时间
		int_end_hour: '',					//结束时间
		eid: 0,								//上课老师id
		rts_id: 0,							//点评模板id
		bid: 0,								//校区id
		lesson_type: 0,						//课程类型：0:班课，1:1对1,2:1对多
		cid: 0,								//班级id
		sid: 0,								//一对一学员id
		sids: [],							//一对多学员id
		lid: 0,								//课程id
		sj_id: 0,							//科目id
		catt_id: 0,							//考勤id
		content: {
//			lesson_content: '',			//上课内容
//			lesson_after_task: '',		//课后作业
//			next_task: ''				//下次课内容
		},		
		review_file: [],					//点评附件					
		review_student: [					//点评学员
//			{
//				sid: 0,						//学员id
//				detail: '',					//点评内容
//				score: 0					//分数
//			}
		]		
	}

export default{
	mixins: [common,globals],
	data () {
		return {
			post: util.copy(emptyObject),
			data: {},
			mode: 'edit',
			images: {
				serverId: [],
				localId: [],
				urls: []
			}
		}
	},
	computed: {
		currentTpl () {
			let list = this.$store.state.gvars.review_tpl_setting,
				rts_id = this.getTplId(),
				result = {}
				
			if(_.isArray(list)&&list.length>0){
				let obj = list.find(l=>l.rts_id==rts_id)
				if(obj&&rts_id>0){
					result = obj						
				}else{
					result = list[0]
				}				
			}else if(_.isObject(list)&&!_.isEmpty(list)){
				result = list
			}
			
			return result
		},
		content_fields () {
			let result = {},
				fields = this.currentTpl.setting?this.currentTpl.setting.common_fields:{}
			
			for(let s in fields){
				let obj = fields[s]
				if(obj.enable==1){						
					result[s] = {}
					result[s].label = obj.label
					result[s].value = obj.tpl
					result[s].student_view = obj.student_view
				}					
			}
			
			return result
		},
		scores () {
			let result = {},
				fields = this.currentTpl.setting?this.currentTpl.setting.score_fields:{}
			for(let s in fields){
				let obj = fields[s]
				if(obj.enable==1){						
					result[s] = {}
					result[s].label = obj.label
					result[s].value = obj.default
				}					
			}
			
			return result
		}
	},
	methods: {
		setPost () {
			if(!_.isEmpty(this.data)){
				//按考勤记录点评
				this.post.catt_id 			= this.data.catt_id
				this.post.bid 				= this.data.bid
				this.post.cid 				= this.data.cid
				this.post.lid 				= this.data.lid
				this.post.eid 				= this.data.eid
				this.post.sj_id 			= this.data.sj_id
				this.post.catt_id			= this.data.catt_id
				this.post.int_day 			= this.data.int_day
				this.post.lesson_type 		= this.data.lesson_type
				this.post.int_end_hour 		= this.data.int_end_hour
				this.post.int_start_hour 	= this.data.int_start_hour
				this.post.rts_id 			= this.getTplId()
				
				this.setContent()
				this.getStudents()
			}
		},		
		getStudents () {
			this.$rest('student_attendances?catt_id='+this.id).get({pagesize: 100})
			.success(data=>{
				this.deal_students(data.list)
			})
		},
		chooseFile () {
			let length = this.post.review_file.length
			
			this.chooseImage(9 - length,true)
			.then(localIds=>{
//				alert(JSON.stringify(localIds))
				localIds.forEach(l=>{					
					this.post.review_file.push({
						file_id: l.file_id,
		        		file_url: l.file_url,
		        		file_type: l.file_type
					})	
				})				
			})
		},
		handleRemove (item) {
			let index = this.post.review_file.indexOf(item)
			if(index>-1){
				this.post.review_file.splice(index,1)
			}
		},
		getTplId () {
			let [result,info] = [0,this.data]
				 
			if(!_.isEmpty(info)){
				let list = this.$store.state.gvars.review_tpl_define,
					[obj, cid, lid, sj_id] = [{}, info.cid, info.lid, info.sj_id]
				
				if(list&&list.length>0){
					obj = list.find(l=>l.cid==cid||l.lid==lid||l.sj_id==sj_id)
					
					if(obj){
						result = obj.rts_id
					}						
				}
			}
			return result
		},
		scoreLabel (index) {
			let scoreFields = this.currentTpl.setting?this.currentTpl.setting.score_fields:{}
			if(scoreFields){
				return scoreFields['score'+(index>0?index:'')].label				
			}else{
				return ''
			}
		},
		setContent () {
			let content = this.content_fields
			if(!_.isEmpty(content)){
				for(let c in content){
					this.$set(this.post.content,c,content[c].value)
//					this.post.content[c] = content[c].value
				}
			}
		},
		deal_students (data) {
			if(data&&data.length>0){
				this.post.review_student = []
				data.forEach(d=>{
										
					let obj = {}
					
					if(d.student){						
						obj.sid = d.student.sid,
						obj._student_name = d.student.student_name
						obj._photo_url = d.student.photo_url
					}
					
					obj.detail = this.currentTpl.setting?this.currentTpl.setting.student_detail.default.tpl:''
					
					for(let s in this.scores){
						obj[s] = this.scores[s].value
					}
					//排除请假学员
					if(d.is_leave==1){
						obj.$delete = true
						obj.is_leave = 1
					}
					this.post.review_student.push(obj)
				})				
			}
		},
		handleSubmit () {
			this.$rest('reviews')
			.post(this.post)
			.success(data=>{
				this.toast('点评成功','success')	
				this.$router.go(-1)
			}).error(response=>{
				this.toast(response.body.message||'点评失败','warn')			
			})
		}
	}
	
}


