
import Vue from 'vue'
import comment from './comment.mixin'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import CellContent from './cell-content.vue'
import SelectFile from 'c%/SelectFile.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import MyRater from 'c%/MyRater.vue'

export default{
mixins: [common,globals,comment,wxjssdk],
components: {
Cell,
Rater,
Group,
XButton,
Flexbox,
MyRater,
SelectFile,
CellContent,
FlexboxItem,
Icon
},
data () {
return {
data: {},
id: this.$route.params.id,
students: [],
ready: false
}
},
mounted () {
this.init()
},
methods: {
deleteLeaveStudent(index) {

this.post.review_student.splice(index,1);
},
init () {
this.init_data()
},
setPostByStorage () {
let data = Vue.localStorage.get(this.commentKey)

if(data) {
this.post = JSON.parse(data)
}
},
saveToStorage () {
Vue.localStorage.set(this.commentKey,JSON.stringify(this.post))
this.toast('保存成功！','success')
},
delStorage () {
Vue.localStorage.remove(this.commentKey)
},
init_data () {
let tempData = Vue.localStorage.get(this.commentKey)
this.$rest(this.data_uri).get()
.success(data=>{
this.data = data.list[0]
if(tempData){
this.setPostByStorage()
}else{
if(!this.data.review){
this.setPost()
}
}


this.ready = true
})
},
handleSubmit () {
this.showLoading()
this.$rest('reviews')
.post(this.post)
.success(data=>{
this.hideLoading()
this.toast('点评成功','success')
this.delStorage()
this.$router.go(-1)
}).error(response=>{
this.toast(response.body.message||'点评失败','warn')
})
}
},
computed: {
commentKey () {
return 'comment_data_' + this.id
},
title () {
let result = '',
data = this.data
result += `应到: ${data.need_nums}, 实到: ${data.in_nums},请假:${data.leave_nums}, 试听: ${data.trial_nums}`
result += '（*试听学员无法点评）'
return result
},
lesson () {
let lessons = this.$store.state.gvars.lessons,
lid = this.data.lid,
result = {}
if(lessons){
result = lessons.find(l=>l.lid==lid)
}
return result
},
data_uri () {
return 'class_attendances?catt_id=' + this.id
}
}
}



